.carousel-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
}

.carousel-item {
  text-align: center;
  padding: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.carousel-item img {
  width: 136px;
  height: 160px;
  margin-bottom: 10px;
}

.carousel-item h3 {
  font-size: 16px;
  font-weight: bold;
}

.carousel-item p {
  font-size: 14px;
}

.price-promo {
  color: #e91e63;
  font-family: 'Poppins', sans-serif;
  font-weight: bolder;
  display: flex;
}

.price-promo span {
  margin-right: 5px;
  font-size: 1rem;
  font-weight: 700;
    line-height: 1.6;
    letter-spacing: 0.0156rem;
    font-weight: var(--ib-fontWeights-bold);
}

.carousel-item del {
  font-family: 'Poppins', Verdana, sans-serif;
  font-weight: lighter;
  font-size: 12px;
  color: #757575;
  font-size: 0.8rem;
  font-weight: var(--ib-fontWeights-semibold);
  margin-left: var(--ib-space-1);
  margin-top: 0.3rem;
  white-space: nowrap;
}


.carousel-item .nome-produto {
  font-family: 'Poppins', Verdana, sans-serif;
  color: #757575;
}

#ofertas {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

#oferta-img {
  width: 2.5rem;
  height: 2.5rem;
  color: #43b02a;
  margin-right: 5px;
}
.carousel-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
  position: relative; 
}
.slick-prev:before,
.slick-next:before {
  color: black;
 
}

.slick-prev:before,
.slick-next:before {
  color: rgb(20, 155, 47);
  
  
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  background-color: rgb(255, 255, 255);
}
